import React from "react";
import { connect } from "react-redux";
import { withFormik, Form, Field } from "formik";
import * as Yup from "yup";
import { LoginService } from "../services/auth";
import { login } from "../redux/actions/auth";
import decode from "jwt-decode";

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            resetPassword: false
        }
    }

    resetPassword = () => {
        this.setState({ resetPassword: true })
    }
    handleSubmit = (event) => {
        
        event.preventDefault();
        const loginService = new LoginService();
        const { values, setSubmitting, setFieldError } = this.props;
        const { resetPassword } = this.state;

        if (!resetPassword) {
            debugger;
            setSubmitting(true)
            loginService.login(values).then(data => {
                setSubmitting(false);
                if (!data['success']) {
                    setFieldError("login", data['message']);
                } else {
                    const token = decode(data.token)
                    this.props.dispatch(login(token));
                    // let url = rest.props.url;
                    this.props.history.push("/");
                }
            }).catch(err => {
                console.log(err);
            })
        } else {
            setSubmitting(true)
            loginService.resetPassword(values).then((data) => {
                setSubmitting(false)
                if (data['success']) {
                    setFieldError("login", data['message']);
                    this.setState({ resetPassword: false });
                } else {
                    setFieldError("login", data['message']);

                }
            })
        }
    }
    render() {
        const { resetPassword } = this.state;
        const { errors, touched, isSubmitting } = this.props;
        return (
            <div className="container">
                <div className="row">
                    <div className="col-md-4 form">
                        <h3 className={resetPassword ? "text-center reset-password" : "text-center login"}>{resetPassword ? "Reset Password" : "Login!"}</h3>
                        {errors.login && <p className="text-danger">{errors.login}</p>}
                        <Form name="login" onSubmit={this.handleSubmit}>
                            <div className="form-group">
                                <Field type="text" name="userName" placeholder="Username" className="form-control" />
                                {errors.userName && touched.userName && <p className="text-danger">{errors.userName}</p>}
                            </div>
                            <div className="form-group">

                                <Field type="password" name={resetPassword ? "newPassword" : "password"} placeholder={resetPassword ? "newPassword" : "password"} className="form-control" />
                                {errors.password && touched.password && <p className="text-danger">{errors.password}</p>}
                            </div>
                            <p className="clearfix"><button type="button" className="btn btn-link float-right" onClick={this.resetPassword}>{!resetPassword ? "Reset Password" : ""}</button></p>
                            <button className={resetPassword ? "btn btn-block btn-primary" : "btn btn-block btn-success"} disabled={isSubmitting}>{resetPassword ? "Reset Password" : "Login!"}</button>
                        </Form>
                    </div>
                </div>
            </div>
        )
    }
}
const LoginFormik = withFormik({
    mapPropsToValues({ username }) {
        return {
            userName: username || '',
            password: "",
            newPassword: ""
        }
    },
    validationSchema: Yup.object().shape({
        userName: Yup.string().required('User name is required'),
        password: Yup.string().min(4, "Must be 4 character in length").required('Password is required'),
    })

})(Login);

const mapStateToProps = (state) => ({
    url: state.auth
})

export default connect(mapStateToProps)(LoginFormik);
