import React from "react";
import {Router, Switch, Route ,Redirect} from "react-router-dom";
import createHistory from "history/createBrowserHistory";
import Home from "../components/home";
import Navbar from "../components/navbar";
import Login from "../components/login";
import Register from "../components/register";
import CreateBlog from "../components/createblog";
import PrivateRoute from "./private-route";
import {connect} from "react-redux";
import decode from "jwt-decode";
import Blog from "../components/Blog";
import NotFound from "../components/notfound";

export const history = createHistory();

const AppRoutes = (props) => {
    let userInfo = !!!(Object.keys(props.userData).length) ? !!localStorage.getItem("token") && decode(localStorage.getItem("token")): props.userData;
    console.log(userInfo);
    return (
        <Router history={history}>
            <div>
                <Navbar  userData = {userInfo}/>
                <Switch>
                    <Route path="/" component={Home} exact />
                    <Route path="/register" component={Register} />
                    <Route path="/login" component={Login} />
                    <PrivateRoute path="/blog" component ={CreateBlog} exact/>
                    <Route path="/blog/:id" component = {Blog} exact/>
                    <Route component={NotFound} />
                </Switch>
            </div>
        </Router>
    )
}

const mapStateToprops = (state)=>({
    userData:state.auth
})
export default connect(mapStateToprops)(AppRoutes);