import React from "react";
import {NavLink} from "react-router-dom";
import { titlCase } from "../utils/helper";


class ProductList extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="col-md-4" style={{marginTop:"10px"}}>
                <div className="card" style={{height:"100%"}}>
                    <div className="card-header">
                        <h3>
                            {this.props.post.title}
                        </h3>
                    </div>
                    <div className="card-body">
                        <p className="card-title">Author: {titlCase(this.props.post.author.userName)}</p>
                        <p className="card-text">{this.props.post.description.substring(0, 60) + "..."}</p>
                        <NavLink to={`/blog/${this.props.post._id}`} className="btn btn-link">Read More</NavLink>
                    </div>
                </div>
            </div>
        )
    }
}

export default ProductList;