import React from "react";

import img from "../img/loading.gif";

class Loading extends React.Component {
   constructor(props){
       super(props);
   }

    render() {
        return (
            <div>
               {this.props.loading &&  <img className="loading-icon" src={img} alt="loading" />}
            </div>
        )
    }
}
export default Loading;