import React from "react";
import decode from "jwt-decode";

export class LoginService extends React.Component {
    constructor(props) {
        super(props);
      
    }
    loginEndpoint = "/api/login"

    login(loginData) {
        return fetch(this.loginEndpoint, {
            method: "POST",
            body: JSON.stringify(loginData),
            headers: {
                "Content-Type": "application/json"
            }
        }).then(res => res.json())
            .then(data => {
                // let token = localStorage.getItem("token");
                // if (!token)
                localStorage.setItem("token", data.token);
                return data;
            })
            .catch(err => {
                return err;
            })
    }

    register(userData) {
        return fetch("/api/register", {
            method: "POST",
            body: JSON.stringify(userData),
            headers: {
                "Content-Type": "application/json"
            }
        }).then(res => res.json())
            .then(data => {
                if (data['success']) {
                    localStorage.setItem("token", data.token);
                    return data;
                }
            }).catch(err => {
                return err;
            })
    }

    resetPassword(resetInfo) {
        return fetch("/api/reset-password", {
            method: "PUT",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify(resetInfo)
        }).then(res => res.json())
            .catch(err => err)
    }

    isLoggedIn() {
        try {
            let token = localStorage.getItem("token");
            let decodeToken = token && decode(token);
            let isExpired = (decodeToken.exp < Date.now() / 1000)
            return !!token && !isExpired;

        } catch (exp) {
            console.log(exp);
        }
    }

    userDetail() {
        try {
            let token = localStorage.getItem("token");
            let decodeToken = token && decode(token);
            return decodeToken;

        } catch (exp) {
        }
    }

    static logout() {
        localStorage.removeItem("token");
    }


}