import React from "react";
import { connect } from "react-redux";
import decode from "jwt-decode";
import { withFormik, Field, Form } from "formik";
import * as Yup from "yup";
import { AddReview, getPostComments, deleteComment } from "../services/post";
import * as moment from "moment";
import { LoginService } from "../services/auth";
import { history } from "../routes/routes";
import Loading from "./loading";

class Review extends React.Component {
    constructor(props) {
        super(props)
        this.userId = props.userId;
        this.reviewerId = props.reviewerId
        this.state = {
            show: false,
            comments: [],
            pageNum: 0,
            scrolling: false,
            totalPage: 0,
            loading:false
        }
    }
    componentDidMount = () => {
        this.getAllComments();
        window.addEventListener("scroll", this.handleScroll);
    }
    handleScroll = (event) => {
        const { scrolling, totalPage, pageNum } = this.state;
        if (scrolling) return;
        if (pageNum >= totalPage) return;
        let pageOffset = window.pageYOffset + window.innerHeight;
        let el = document.querySelector("li.list-group-item:last-child");
        if (el) {
            let elOffset = Math.round(el.getBoundingClientRect().top + document.documentElement.scrollTop + el.clientHeight);
            if (pageOffset > elOffset) this.loadMore();
        }
    }

    loadMore = () => {
        this.setState(prevState => ({
            scrolling: true,
            pageNum: prevState.pageNum + 1
        }), this.getAllComments);
    }

    showReview = () => {
        let isLoggedIn = new LoginService().isLoggedIn();
        if (!isLoggedIn) {
            history.push("/login");
        } else {
            this.setState(() => {
                return {
                    show: !this.state.show
                }
            })
        }

    }

    handleSubmit = (event) => {
        const { productId, reviewerId, values, setFieldError, resetForm } = this.props;
        event.preventDefault();
        this.setState({loading:true})
        AddReview(productId, reviewerId, values,this.state.loading).then(data => {
            if (!data['success']) {
                setFieldError("description", data['message']);
                this.setState({loading:false})
            } else {
                resetForm();
                this.setState((prevState) => ({ 
                    comments:[data.comment,...prevState.comments],
                    show: false ,
                    loading:false}));
                
            }
        })
    }

    getAllComments = () => {
        debugger;
        const { productId } = this.props;
        const { pageNum } = this.state;
        this.setState({loading:true});
        getPostComments(productId, pageNum,this.state.loading).then(data => {
            if (data['success']) {
                this.setState((prevState) => ({
                    comments: [...prevState.comments, ...data.reviews],
                    scrolling: false,
                    totalPage: data.total,
                    loading:false
                }))
            }else{
                this.setState({loading:false})
            }

        })
    }

    deleteReview = (comment) => {
        let commentIdx = comment._id;
        deleteComment(comment._id).then((data) => {
            if (data['success']) {
                this.setState(prevState => ({
                    comments: prevState.comments.filter(comment => comment._id !== commentIdx)
                }))
            }
        })
    }
    render() {
        const { errors, touched } = this.props;
        return (
            <div className="container">
            <Loading loading={this.state.loading}/>
                <div className="row">
                    <a className="btn btn-link text-info" style={{ fontSize: "15px", fontWeight: "600", fontFamily: "monospace" }} onClick={this.showReview}>Add Review</a>
                </div>
                {this.state.show &&
                    (<div className="row">
                        <div className="col-8" style={{ padding: "0" }}>
                            {errors.description && touched.description && <p className="text-danger">{errors.description}</p>}
                            <Form onSubmit={this.handleSubmit}>
                                <div className="form-group">
                                    <Field component="textarea" name="description" placeholder="Your Comments" className="form-control" row="30" style={{ resize: "none" }} />

                                </div>
                                <div className="form-group">
                                    <button className="btn btn-outline-secondary">Post Comment</button>
                                </div>
                            </Form>
                        </div>

                    </div>)
                }
                {this.state.comments.length ? (
                    <div className="row mb-2">
                        <div className="col-sm" style={{ padding: "0" }}>
                            <div className="card">
                                <div className="card-header bg-info">
                                    <h5 style={{ color: "white" }}>Comments:</h5>
                                </div>
                                <div className="card-body">
                                    <div className="list-group">
                                        {this.state.comments.map(comment => {
                                            return (
                                                <li key={comment._id} className="list-group-item mb-2">
                                                    <p className="card-text clearfix">
                                                        {comment.description}
                                                        {comment.author == this.reviewerId && <button className="btn btn-sm btn-danger float-right" onClick={this.deleteReview.bind(this, comment)}>Delete</button>}
                                                    </p>
                                                    <p className="float-right" style={{ color: "lightseagreen" }}><em>On {moment(comment.created).format("MM/DD/YYYY hh:mm a")} By {comment.authorName}</em></p>
                                                </li>
                                            )
                                        })}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ) : (
                        <h4>No comments on this Post Yet</h4>

                    )}
            </div>
        )
    }
}
const mapStateToProps = (state) => ({
    reviewerId: state.auth.user && state.auth.user._id || localStorage.getItem("token") && decode(localStorage.getItem("token")).user._id
});

const reviewForm = withFormik({
    mapPropsToValues(values) {
        return {
            description: ""
        }
    },
    validationSchema: Yup.object().shape({
        description: Yup.string().required("Please add some comments")
    }),
    // handleSubmit(values,props) {
    //     console.log(props);
    // }
})(Review)

export default connect(mapStateToProps)(reviewForm);