function ajaxService(url,method,body,loading,isSecured=true){
    let reqestParams = {};
    reqestParams.headers = {
        "Content-type":"application/json"
    }
    if(isSecured){
        reqestParams.headers['authorization'] = localStorage.getItem("token");
    }


  reqestParams.method = method;
  reqestParams.body = method=="POST" || method=="PUT" ?  JSON.stringify(body) : "";

   method=="GET"  && delete reqestParams.body;


    return fetch(url,reqestParams).then(res=>res.json()).catch(err=>err);
}

export const createPost = (values)=>{
    return ajaxService(`/api/post`,"POST",values);
}

export const AddReview = (productId,reviewerId,description,loading)=>{
    debugger;
    return ajaxService(`/api/${productId}/review/${reviewerId}`,"POST",description,loading);
}

export const getPostComments = (productId,pageNum=0,loading)=>{
    return ajaxService(`/api/${productId}/reviews?pageNum=${pageNum}`,"GET",null,loading,false);

}

export const deleteComment = (reviewId)=>{
    return ajaxService(`/api/${reviewId}`,"DELETE",null);

}