import React from "react";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import { saveUrl } from "../redux/actions/auth";
import { LoginService } from "../services/auth";
import { history } from "../routes/routes";


class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: this.props.userData
        }
        const { userData } = this.props;
        this.isLoggedIn = userData && !!Object.keys(userData).length;
        this.userName = this.props.userData && this.props.userData.user && `${this.props.userData.user.firstName} ${this.props.userData.user.lastName}`;
        this.state = {
            userData: this.props.userData,
            isLoggedIn: this.isLoggedIn,
            userName: this.userName,
            shMobilIcon:false
        }
    }

     showMobileIcon = ()=>{
         console.log("hello")
         this.setState(()=>{
             return {
                 shMobilIcon: !this.state.shMobilIcon
             }
         })
     }
    componentWillReceiveProps(nextProp) {
        const userName = nextProp.userData && nextProp.userData.user && `${nextProp.userData.user.firstName} ${nextProp.userData.user.lastName}`;
        this.setState(() => ({
            userData: nextProp,
            isLoggedIn: nextProp && !!Object.keys(nextProp).length,
            userName: userName
        }))
    }

    render() {
        const { isLoggedIn, userName ,shMobilIcon  } = this.state;
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <NavLink className="navbar-brand" to="/" activeClassName="active">Blog Post</NavLink>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation" onClick={this.showMobileIcon}>
                    <span className="navbar-toggler-icon"></span>
                </button>     
                <div className={shMobilIcon ? "collapse navbar-collapse show": "collapse navbar-collapse"} id="navbarText">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/" activeClassName="active">Home <span className="sr-only">(current)</span></NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/blog" activeClassName="active">Create Blog <span className="sr-only">(current)</span></NavLink>

                        </li>
                        <li className="nav-item">
                            {!isLoggedIn && <NavLink to="/register" className="nav-link" activeClassName="active">Register</NavLink>}

                        </li>
                        <li className="nav-item">

                            {!isLoggedIn && <NavLink to="/login" className="nav-link" activeClassName="active">Login</NavLink>
                            }
                        </li>
                    </ul>
                    <ul className="navbar-nav">
                        {isLoggedIn && <div><span>Welcome {userName}</span> <button className="btn btn-sm btn-info" onClick={() => {
                            LoginService.logout();
                            this.setState(() => ({
                                userData: "",
                                isLoggedIn: false,
                                userName: ""

                            }));
                            history.push("/");
                        }}>Logout</button></div>}

                    </ul>

                </div>
            </nav>
        )
    }
}



export default connect()(Navbar);