
const defaultState = {success:false,message:"",post:[]};

const ProductReducer = (state = defaultState, action)=>{
    debugger;
      switch(action.type){
          case "ALLPRODUCT":
           return {
               ...state,
               ...action.product
           }
          case "BLOG":
             return {
                 ...action.blog
             }
           default:
              return state;
      }
}

export default ProductReducer;