import React from "react";
import { connect } from "react-redux";
import { getProducts } from "../redux/actions/product-action";
import ProductList from "./product-list";
import Navbar from "./navbar";
import Loading from "./loading";

class Home extends React.Component {
    state = {
        loading: false,
        product: {}
    }
    constructor(props) {
        super(props)
    }

    componentWillMount = () => {
        this.setState(() => ({ loading: true }));
        this.props.dispatch(getProducts()).then(data => {
            this.setState(() => ({ loading: false }));
            this.setState(() => {
                return {
                    product: this.props.products
                }
            })
        })
    }


    render() {
        console.log(this.state.loading)
        const list = () => {
            if (this.state.loading) {
                return (
                <div>
                    <Loading />
                    <p>Loading Please wait...</p>
                </div>);
            } else {
                return (this.state.product && this.state.product.post && this.state.product.post.map(po => {
                    return <ProductList key={po._id} post={po} />
                }));
            }
        }

        return (
            <div>
                <div className="container">
                    <div className="row">
                        <div className="jumbotron">
                            <header>
                                <h1>The Blog Post</h1>
                                <p>Post your own Blog.Search through some of the best blogs posted by hunderds of user. Sign up and start posting today. Share Knowledge and gain knowledge.Its not always complicated,its simple thing we don't solve the right way. Here we have pleanty of blogs where people have figured out the right way. Read them and share yours own.</p>
                            </header>
                        </div>
                    </div>
                    <div className="row">
                        {list()}
                    </div>
                </div>
            </div>
        );
    }
}
const mapStatetoProps = (state) => ({
    products: state.products,
    userData: state.auth
})

export default connect(mapStatetoProps, null)(Home);

