import React from "react";
import { Form, Field, withFormik, } from "formik";
import * as Yup from "yup";
import { createPost } from "../services/post";

const CreateBlog = () => {

    return (
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <h3>Post your Blog</h3>
                    <Form>
                        <div className="form-group">
                            <label htmlFor="title">Title</label>
                            <Field type="text" name="title" className="form-control" />
                        </div>
                        <div className="form-group">
                            <Field component="textarea" name="description" className="form-control" />

                        </div>
                        <button className="btn btn-block btn-secondary">Post Blog</button>
                    </Form>
                </div>
            </div>
        </div>
    )
}

const createBlogFormik = withFormik({
    mapPropsToValues(values) {
        return {
            title:"",
            description:""
        }

    },
    validationSchema: Yup.object().shape({

    }),
    handleSubmit(values, {...rest}) {
        createPost(values).then(data => {
            if (data['success'])
                rest.props.history.push("/");
        })
    }

})(CreateBlog)

export default createBlogFormik;