import React from "react";
import { withFormik, Field, Form } from "formik";
import * as Yup from "yup";
import decode from "jwt-decode";
import {connect} from "react-redux"
import { LoginService } from "../services/auth";
import { login } from "../redux/actions/auth";

const Register = ({ errors, touched,...props }) => (
    <div className="container">
       {errors.register && <p className="text-danger">{errors.regiter}</p> }
        <Form name="register">

            <div className="row">
                <div className="col-md-6 mb-3">
                    <label htmlFor="firstName">First name</label>
                    <Field type="text" className="form-control" name="firstName" id="firstName" />
                    {errors.firstName && touched.firstName && <div className="invalid-feedback">
                        Valid first name is required.
                </div>}
                </div>
                <div className="col-md-6 mb-3">
                    <label htmlFor="lastName">Last name</label>
                    <Field type="text" name="lastName" className="form-control" id="lastName" />
                    <div className="invalid-feedback">
                        Valid last name is required.
                </div>
                </div>
            </div>

            <div className="mb-3">
                <label htmlFor="username">Username</label>
                <div className="input-group">
                    <div className="input-group-prepend">
                        <span className="input-group-text">@</span>
                    </div>
                    <Field type="text" name="userName" className="form-control" id="username" placeholder="Username" />
                    <div className="invalid-feedback" style={{ width: "100%" }}>
                        Your username is required.
                </div>
                </div>
            </div>

            <div className="mb-3">
                <label htmlFor="password">Password</label>
                <Field type="password" name="password" className="form-control" id="password" />
                <div className="invalid-feedback">
                    Valid last name is required.
                </div>
            </div>
            <div className="mb-3">
                <label htmlFor="email">Email <span className="text-muted">(Optional)</span></label>
                <input type="email" name="email" className="form-control" id="email" placeholder="you@example.com" />
                <div className="invalid-feedback">
                    Please enter a valid email address htmlFor shipping updates.
              </div>
            </div>

            <div className="mb-3">
                <label htmlFor="address">Address</label>
                <Field type="text" name="address.add1" className="form-control" id="address" placeholder="1234 Main St" />
                <div className="invalid-feedback">
                    Please enter your shipping address.
              </div>
            </div>

            <div className="row">
                <div className="col-md-5 mb-3">
                    <label htmlFor="country">Country</label>
                    <Field component="select" name="address.country" className="custom-select d-block w-100" id="country" >
                        <option value="">Choose...</option>
                        <option>United States</option>
                    </Field>
                    <div className="invalid-feedback">
                        Please select a valid country.
                </div>
                </div>
                <div className="col-md-4 mb-3">
                    <label htmlFor="state">State</label>
                    <Field component="select" name="address.state" className="custom-select d-block w-100" id="state">
                        <option value="">Choose...</option>
                        <option value="CA">California</option>
                        <option value="TX">Texas</option>
                    </Field>
                    <div className="invalid-feedback">
                        Please provide a valid state.
                </div>
                </div>
                <div className="col-md-3 mb-3">
                    <label htmlFor="zip">City</label>
                    <Field type="text" name="address.city" className="form-control" id="zip" placeholder="" />
                    <div className="invalid-feedback">
                        Zip code required.
                </div>
                </div>
            </div>
            <div className="col-md-3">
                <button className="btn btn-lg btn-primary">Submit</button>
            </div>
        </Form>

    </div>
);
const RegisterForm = withFormik({
    mapPropsToValues(values) {
        return {
            firstName: "",
            lastName: "",
            userName: "",
            password: "",
            email: "",
            address: {
                city: "",
                state: "",
                country: ""
            }
        }

    },
    validationSchema: Yup.object().shape({
        firstName: Yup.string().required("first name is required"),
        lastName: Yup.string().required("lastName name is required"),
        userName: Yup.string().required("user name is required"),
        password: Yup.string().min(4, "Must be atleast four character").required("Password is required")
    }),
    handleSubmit(values, { setSubmitting, setFieldError, ...rest }) {
        const loginService = new LoginService();
        setSubmitting(true)
        loginService.register(values).then(data => {
            setSubmitting(false);
            if (!data['success']) {
                setFieldError("register", data['message']);
            } else {
                const token = decode(data.token);
                rest.props.dispatch(login(token));
                rest.props.history.push("/");
            }
        }).catch(err => {
            console.log(err);
        })
    }

})(Register)

export default connect()(RegisterForm);