// const initState = decode(localStorage.getItem("token"));


const AuthReducer = (state={},action)=>{
    debugger;
    switch(action.type){
        case "LOGIN":
           return {
            ...state,
            ...action.data
           }

        case "REDIRECTURL":
            return action.url;

         default: 
            return state;  
    }
}

export default AuthReducer;