import React from "react";
import { connect } from "react-redux";
import * as moment from "moment";
import { getSinglePost } from "../redux/actions/product-action";
import Review from "./review";
import Loading from "../components/loading";
import { loadConfig } from "../utils/helper";


class Blog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading:false
        }
    }
    componentDidMount() {
        this.setState({loading:true})
        const blogId = this.props.match.params.id;
        this.props.blog(blogId).then(data => {
            this.setState({loading:false})
        })
    }
    render() {
        const { post } = this.props.eachPost;
        return (
            <div className="container" style={{ marginTop: "2%" }}>
                {this.state.loading && <Loading />}

                <div className="row">
                    <div className="col-sm">
                        <div className="card text-center">
                            <div className="card-header">
                                <h5 className="card-title">{post.title}</h5>

                            </div>
                            <div className="card-body">
                                <p className="card-text">{post.description}</p>
                            </div>
                            <div className="card-footer text-muted">
                                <em>Created On: {moment(post.created).format("MM/DD/YYYY")}</em>
                                <div className="text-right">
                                    <a href="#" className="link link-danger" style={{ textTransform: "capitalize" }}>{`${post.author && post.author.userName}'s Profile`}</a>

                                </div>
                            </div>
                        </div>
                        <Review productId={this.props.match.params.id} userId={post.author && post.author._id} />
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProp = (state) => ({
    eachPost: state.products
})
const mapPropsToDispatch = (dispatch) => ({
    blog: (id) => dispatch(getSinglePost(id))
})
export default connect(mapStateToProp, mapPropsToDispatch)(Blog);